.grid-list {
    width: 100%;

    .grid-list-item {
        width: calc(100% / 5 - 2px) !important;
        height: auto;

        &:before {
            content: "";
            width: 1px;
            margin-left: -1px;
            float: left;
            height: 0;
            padding-top: 100%;
        }

        &:after {
            content: "";
            display: table;
            clear: both;
        }
    }
}

.profile-header {
    padding: 40px 0;

    .block {
        .avatar {
            border-radius: 50%;
            size: 80px;
        }

        .user-info {
            margin-left: 40px;
            color: var(--color2);

            .nickname {
                font: 400 15px/120% var(--font2);
                letter-spacing: 1px;

            }

            .indificator {
                margin-top: 5px;
                font: 400 12px/120% var(--font2);
                letter-spacing: 1px;
            }
        }
    }
}

.profile-cats {
    ul {
        padding:20px 0 40px;
        li {
            margin-right: 20px;

            a {
                font: 400 12px/120% var(--font2);
                letter-spacing: .3px;
                text-transform: uppercase;
                color:var(--color2);
                opacity: .6;
            }
            &:hover {
                a {
                    opacity: 1;
                }
            }
            &.active {
                a {
                    opacity: 1;
                    color:var(--color5);
                }
            }
        }
    }
}

